import React, { useEffect, useState } from "react";
import { Input, Divider } from "antd";
import Keyboard from "react-simple-keyboard";

interface indexProps{
  getValue:(v:object)=>void
}
interface valueProps{
  hourlyRate: number,
  month: number,
  labor:number,
  health: number,
}


const Index = ({
  getValue =()=>{}
}:indexProps) => {

  const [focusedInput, setFocusedInput] = useState<string| null>(null)
  // keyboard value
  const [inputValue, setInputValue] = useState<valueProps>({
    hourlyRate: 0,
    month: 0,
    labor:0,
    health: 0,
  })

  //  input Keyboard
  const showKeyboard = (type:string)=>{
    setFocusedInput(type)
  }

  // Keyboard 
  const onChangeKeyboard = (v:number)=>{
    if(focusedInput){
      setInputValue((prev)=>({
        ...prev,
        [focusedInput]: v
      }))
    }
  }


  // data
  useEffect(()=>{
    getValue(inputValue)
  },[inputValue])


  //  (input + Keyboard) side effect
  useEffect(()=>{
    const handleClickOutside = (event:MouseEvent) =>{
      // input + Keyboard(className: querySelector)
      if(!document.getElementById("basicInput")?.contains(event?.target as Node)
      && !document.querySelector(".react-simple-keyboard")?.contains(event?.target as Node)
      ){
        //  Keyboard
        setFocusedInput(null)
      }
    }

    // document 
    document.addEventListener('click', handleClickOutside)

    // , 
    return()=>{
      document.removeEventListener('click', handleClickOutside)
    }
  }, [])

  return (
    <>
      <div>
        <Divider orientation="center" plain>
        
        </Divider>
        <div className="d-flex justify-content-center" id="basicInput">
          <div className="basicStyle">
            <div>
              <div className="inputTitle"></div>
              <Input
                id="hourlyRate"
                value={inputValue?.hourlyRate}
                onClick={()=>showKeyboard("hourlyRate")}
                type="number"
                placeholder=""
                //  
                onFocus={(e)=> e.target.blur()}
              />
            </div>
            <div>
              <div className="inputTitle"></div>
              <Input
              id="month"
              value={inputValue?.month}
              onClick={()=>showKeyboard("month")}
              type="number"
              placeholder=""
              //  
              onFocus={(e)=> e.target.blur()}
              />
            </div>
          </div>
          <div className="basicStyle">
            <div>
              <div className="inputTitle"></div>
              <Input
              id="labor"
              type="number"
              value={inputValue?.labor}
              onClick={()=>showKeyboard("labor")}
              placeholder=""
              onFocus={(e)=> e.target.blur()}
              />
            </div>
            <div>
              <div className="inputTitle"></div>
              <Input
              id="health"
              type="number"
              value={inputValue?.health}
              onClick={()=>showKeyboard("health")}
              placeholder=""
              onFocus={(e)=> e.target.blur()}
              />
            </div>
          </div>
        </div>
      </div>

      <div
        id="keyboard"
        className={focusedInput 
          ? "d-flex justify-content-center mt-3"
          : "d-none"}
      >
          <Keyboard
            onChange={(v) =>  {
              onChangeKeyboard(parseInt(v, 10))
            }}
            // input valueinput value
            inputName={focusedInput ?? 'keyboard'}
            theme="hg-theme-default hg-layout-numeric numeric-theme"
            layout={{
              default: [
                '1 2 3', '4 5 6', '7 8 9', '0 {bksp}'
              ],
            }}
            display={{
              '{bksp}': ''
            }}
          />
      </div>
    </>
  );
};
export { Index };
